  import cmdAvatar from "@/components/cmd-avatar/cmd-avatar.vue"
  import cmdIcon from "@/components/cmd-icon/cmd-icon.vue"
  import cmdCellItem from "@/components/cmd-cell-item/cmd-cell-item.vue"
  import uniNavBar from "@/components/uni-nav-bar/uni-nav-bar.vue"
  export default {
    components: {
      cmdAvatar,
      cmdCellItem,
      cmdIcon,
	  uniNavBar
    },
    data() {
      return {};
    },
    methods: {
      /**
       * 打开用户信息页
       */
	  back() {
	  	uni.navigateBack({
	  		delta: 1
	  	})
	  },
      fnInfoWin() {
        uni.navigateTo({
          url: 'info'
        })
      },
	  fnClick(type){
	    if(type == 'map'){
	      uni.navigateTo({
	        url:'./map'
	      })
	    }
		if(type == 'dinningCode'){
		  uni.navigateTo({
		    url:'./dinningCode'
		  })
		}
		if(type == 'new'){
		  uni.navigateTo({
			url:'../new/index'
		  })
		}
		if(type == 'shop'){
			uni.navigateTo({
			url:'../shop/tabBar/home/home'
			})
		}
		if(type == 'identify'){
			uni.navigateTo({
				url:"../identify/identify"
			})
		}
		if(type == 'conference'){
			uni.navigateTo({
				url:"../appointment/conference"
			})
		}
    },
	checkVersion(){
		uni.showToast({
			icon:"none",
			duration:3000,
			title:"当前已是最新版本!"
		})
	}
  },
  }
